body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #ffffff;
}

html {
  height: 100%;
}
/* Overall Nav Bar Styles */

.nav-bar-outer {
    position: fixed;
    background-color: white;
    width: 100%;
    top: 0;
    left: 0;
    height: 10vh;
    z-index: 999; 
}

.nav-bar {
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 100%;
    max-width: 1440px;
    height: 10vh;
    color: #DB4949;
    letter-spacing: 1.2px;
    display: flex;
    align-items: center;
}

/* Right Menu Styles */

#menu-list {
    list-style-type: none;
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    text-decoration: none;
    font-family: 'Raleway';
    font-size: 1.4em;
    font-weight: 300;
    margin-right: 60px;
}

.spacer {
    flex: 1 1;
}

.menu-list-item {
    padding: 0 0.4rem;
    z-index: 500;
    margin: 0 10px 0 10px;
}

.menu-list-item:hover {
    cursor: pointer;
}

.dropdown {
    visibility: hidden;
    opacity: 0;
    list-style-type: none;
    text-decoration: none;
    position: absolute;
    min-width: 150px;
    z-index: 2;
}

.menu-list-item:hover .dropdown {
    visibility: visible;
    opacity: 1;
}

.menu-link {
    text-decoration: none;
    padding: 6px;
    display: block;
}


/* Dropdown Menu Styles */

.dropdown-item {
    font-size: 0.9em;
    color: #DB4949;
    padding: 6px;

    background-color: white;
    transition: all 0.25s;
    display: block;
    padding: 12px 16px;
    text-decoration: none;
}

.dropdown li:last-child .dropdown-item {
    -moz-border-radius-bottomleft: 6px;

    border-bottom-right-radius: 6px;
    border-bottom-left-radius: 6px;
}

.dropdown li:first-child .dropdown-item {
    -moz-border-radius-topleft: 6px;

    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
}

/* Just for non-link test sub menu items for now */
.dropdown li:last-child  {
    -moz-border-radius-bottomleft: 6px;

    border-bottom-right-radius: 6px;
    border-bottom-left-radius: 6px;
}

.dropdown li:first-child {
    -moz-border-radius-topleft: 6px;

    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
}

/* */

.dropdown-item:hover {
    background-color: #DB4949;;
    color: white;
}


/* Left Menu (logo side) Styles */

#menu-left {
  margin-left: 1rem;
}

#cog {
    height: 43px;
    width: 43px;
    float: left;
    display: inline;
    margin-right: 15px;
}

#title {
    font-family: 'Roboto Condensed', sans-serif;
    float: left;
    display: inline;
    position: relative;
    top: -2px;
}


/* Navlink Styles */

.inactive {
    color: #DB4949;
    text-decoration: none;
}

.active-logo {
    color: #DB4949;
    text-decoration: none;
}

.active {
    color: #DB4949;
    text-decoration: none;
}


/* Responsive menu visibility */

@media (max-width: 1120px) {
    #menu-list {
        display: none;
    }

    #title {
        display: none;
    }
}



.toggle-button {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 6vh;
  width: 50px;
  background: transparent;
  border: none;
  padding: 0;
  box-sizing: border-box;
}

.toggle-button:hover {
  cursor: pointer;
}

.toggle-button:focus {
  outline: none;
}

.line {
width: 50px;
border: 0;
border-bottom: 3px solid #DB4949;
border-radius: 10px;}

@media (min-width: 1120px) {
  .toggle-button {
    display: none;
  }
}

.side-drawer {
  height: 100%;
  background: white;
  z-index: 200;
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  top: 10vh;
  max-width: 400px;
  font-family: 'Raleway';
  font-size: 1.4em;
  font-weight: 300;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.menu-header {
  font-size: 0.9em;
  color: #474b51;
  background-color: white;
  display: block;
  padding: 12px 16px;
  text-decoration: none;
}

@media (min-width: 1120px) {
  .side-drawer {
  display: none;
}

}


