  min-height: 280px;
  position: absolute;
  left: 51%;
  top: 20px;
}

@media only screen and (min-width: 761px) {
  #description-container {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY( -50% );
            transform: translateY( -50% );
    max-width: 600px;
    width: 40%;
  }
}

